import React from "react";
import loginImg from "../assets/img/loginimg.webp";
import Navbar from "../components/navbar/Navbar.jsx";

export default function Identificarse() {
  return (
    <div>
      <header className="App-header">
        <Navbar />
      </header>
      <div>
        <div className="grid grid-cols-1 sm:grid-cols-2 h-screen w-full">
          <div className="hidden sm:block">
            <img
              className="w-full h-full object-cover"
              src={loginImg}
              alt="loginimagen"
            />
          </div>

          <div className="bg-orange-300 flex flex-col justify-center">
            <form className="max-w-[400px] w-full mx-auto rounded-lg bg-orange-800 p-8 px-8">
              <h2 className="text-4xl dark:text-white text-white font-bold text-center">
                Identificarse
              </h2>
              <div className="flex flex-col py-2">
                <label>Usuario</label>
                <input
                  className="rounded-lg mt-2 p-2 focus:outline-none"
                  type="text"
                />
              </div>
              <div className="flex flex-col py-2">
                <label>Contraseña</label>
                <input
                  className="p-2 rounded-lg  mt-2 focus:outline-none"
                  type="password"
                />
              </div>
              <div className="flex justify-between text-white py-2">
                <p className="flex items-center">
                  <input className="mr-2" type="checkbox" /> Recuerdeme
                </p>
                <p>Recordar Contraseña</p>
              </div>
              <div className="w-full my-5 py-2 bg-white shadow-lg shadow-white text-orange-800 font-semibold rounded-lg text-center">
                
                Iniciar Sesion
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
