// Import Swiper styles
import React from 'react';
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import './Sliderlogo.css';
import logo1 from "../../assets/img/logostech/logo1.webp";
import logo2 from "../../assets/img/logostech/logo2.webp";
import logo3 from "../../assets/img/logostech/logo3.webp";
import logo4 from "../../assets/img/logostech/logo4.webp";
import logo5 from "../../assets/img/logostech/logo5.webp";
import logo6 from "../../assets/img/logostech/logo6.webp";
import logo7 from "../../assets/img/logostech/logo7.webp";
import logo8 from "../../assets/img/logostech/logo8.webp";
import logo9 from "../../assets/img/logostech/logo9.webp";

const Sliderlogo = () => {
  const setting ={    
    dots:false,
    infinite:true,
    autoplay:true,   
    centerMode:true, 
    speed:500,
    slidesToShow: 4,
    slidesToScroll: 4,
    initialSlide:0,
    responsive:[
      {
        breakpoint:1024,
        setting: {
          slidesToShow:3,
          slidesToScroll:3,
          inifite:true,
          dots:true
        }
      },
      {
        breakpoint:600,
        setting:{
          slidesToShow:2,
          slidesToScroll:2,
          initialSlide: 2
        }
      },
      {
        breakpoint:480,
        setting:{
          slidesToShow:1,
          slidesToScroll:1
        }
      }
    ]
  };
  return(
<Slider {...setting}>
  <div>
    <img src={logo1} alt='logo1' className='logo-img'/>
  </div>
  <div>
    <img src={logo2} alt='logo1' className='logo-img'/>
  </div>
  <div>
    <img src={logo3} alt='logo1' className='logo-img'/>
  </div>
  <div>
    <img src={logo4} alt='logo1' className='logo-img'/>
  </div>
  <div>
    <img src={logo5} alt='logo1' className='logo-img'/>
  </div>
  <div>
    <img src={logo6} alt='logo1' className='logo-img'/>
  </div>
  <div>
    <img src={logo7} alt='logo1' className='logo-img'/>
  </div>
  <div>
    <img src={logo8} alt='logo1' className='logo-img'/>
  </div>
  <div>
    <img src={logo9} alt='logo1' className='logo-img'/>
  </div>
</Slider>
  );
}
export default Sliderlogo;