// Import Swiper styles
import "swiper/css";
import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, {
  Navigation,
  Pagination,
  Scrollbar,
  A11y,
  Autoplay,
} from "swiper";
import Slide1 from "../../assets/img/slider/slide1.webp";
import Slide2 from "../../assets/img/slider/slide2.webp";
import Slide3 from "../../assets/img/slider/slide3.webp";
import Slide4 from "../../assets/img/slider/slide4.webp";
import Slide5 from "../../assets/img/slider/slide5.webp";

SwiperCore.use([Navigation, Pagination, Scrollbar, A11y, Autoplay]);

const Slider = () => {
  const params = {
    modules: [Navigation, Pagination, Scrollbar, A11y, Autoplay],
    autoplay: true,
    delay: 5000,
    loop: true,
    spaceBetween: 30,
    slidesPerView: 1,
    keyboard: { enabled: true },
    navigation: true,
    pagination: { clickable: true },
    scrollbar: { draggable: true },
    onSwiper: (swiper) => console.log(swiper),
    onSlideChange: () => console.log("slide change"),
  };

  return (
    <div className="">
      <Swiper {...params}>
        <SwiperSlide>
          <img src={Slide1} alt="Slide 1" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={Slide2} alt="Slide 2" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={Slide3} alt="Slide 3" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={Slide4} alt="Slide 4" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={Slide5} alt="Slide 5" />
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default Slider;
